import { Component, OnInit } from '@angular/core';
import { MngrModalService } from '../services/mngr-modal.service';
import { MngrServiceService } from '../services/mngr-service.service';

@Component({
  selector: 'app-manager-modal',
  templateUrl: './manager-modal.component.html',
  styleUrls: ['./manager-modal.component.css']
})
export class ManagerModalComponent implements OnInit {

  constructor(public service:MngrModalService) { }

  ngOnInit() {
    this.service.getManagers();
  }
onSubmit(){
  this.service.insertManagers(this.service.form.value);
  this.service.form.reset();
  this.service.inFormGroup();
}
onClear(){
  this.service.form.reset();
  this.service.inFormGroup()
}
}
