import { Injectable } from '@angular/core';
import { FormGroup,FormControl } from '@angular/forms';
//import { FormGroup,FormControl } from 'angfire/databse';
import {AngularFireDatabase,AngularFireList} from 'angularfire2/database'


@Injectable({
  providedIn: 'root'
})
export class MngrModalService {
  constructor(public fireb:AngularFireDatabase) { }

  managerList:AngularFireList<any>;

  form:FormGroup=new FormGroup({
    $key:new FormControl(null),
    managerName: new FormControl(''),
    managingDepartment: new FormControl('')

  })
  inFormGroup(){
    this.form.setValue({
      $key:null,
      managerName: '',
      managingDepartment: ''
    })
  }


  getManagers(){
    this.managerList=this.fireb.list('managers');
    return this.managerList.snapshotChanges();
  }
  insertManagers(manager){
    this.managerList.push({
      managerName:manager.managerName,
      managingDepartment:manager.managingDepartment

    })

  }
  updateManager(manager){
    this.managerList.update(manager.$key,
    {
      managerName:manager.managerName,
      managingDepartment:manager.managingDepartment
    });
  }

  deleteManager($key:string){
    this.managerList.remove($key);
  }
}
