import { Injectable } from '@angular/core';
import {HttpClientModule, HttpClient,HttpHeaders} from '@angular/common/http';
import { Observable, of } from 'rxjs';
import {manager} from '../models/mngr.model';
import { tap, map,catchError } from 'rxjs/operators';
import { stringify } from 'querystring';
import { EditMngrsComponent } from '../manager/edit-mngrs/edit-mngrs.component';

const httpOptions={
  headers:new HttpHeaders({'Content-Type':'application/json'})
}
@Injectable({
  providedIn: 'root'
})
export class MngrServiceService {
  formData:manager;
  private serviceURL='https://localhost:44338/api/ManagerDbs';


  deleteManager(tempmanagerID:number):Observable<manager>{
    const deleteurl=`${this.serviceURL}/${tempmanagerID}`;
    //console.log(deleteurl)
    return this.http.delete<manager[]>(deleteurl).pipe(
      tap(_=>console.log(`Deleted manager id = ${tempmanagerID}`)),
      catchError(error=>of(null))
    )
   }
  constructor(private http:HttpClient) {
  }
   
  showManagerList(): Observable<manager[]>{
    return this.http.get<manager[]>(this.serviceURL);
  }

  addmanager(newmanager:manager){
      return this.http.post<manager>(this.serviceURL,newmanager,httpOptions).pipe(
        tap((man:manager)=>console.log(`inserted manager = ${JSON.stringify(man)}`))
        //catchError(error => of(new manager()))
      );
  }

  datapass(manager){
    console.log(manager.managerID);
  }
   
}
