import { Component, OnInit } from '@angular/core';
import {MatIcon} from '@angular/material/icon'
import { MatDialogRef } from '@angular/material';
import { MngrServiceService } from 'src/app/services/mngr-service.service';
import { NgForm } from '@angular/forms';
import { NgModel} from '@angular/forms';

@Component({
  selector: 'app-add-mngrs',
  templateUrl: './add-mngrs.component.html',
  styleUrls: ['./add-mngrs.component.css']
})
export class AddMngrsComponent implements OnInit {

  constructor(public dialogbox:MatDialogRef<AddMngrsComponent>,public service:MngrServiceService) { }

  ngOnInit() {
    this.resetForm();
  }
  resetForm(form?:NgForm){
    //if(form!=null)
   // form.resetForm();

    this.service.formData={
      managerID:null,
      managerName:'',
      managingDepartment:''
    }
  }
onClose(){
  this.dialogbox.close();
}
onSubmit(form:NgForm){
this.service.addmanager(form.value).subscribe(res=>
  {
    this.resetForm(form);
    //alert(res);
  })
}
}
